<template>
  <div class="container">
    <h1>Users</h1>
    <!--CREATE PLANET HERE-->
    <div class="create-user">
      <label for="create-user">Register</label>
      <input type="text" id="create-user" v-model="name" placeholder="User Name">
      <input type="text" id="create-email" v-model="email" placeholder="User email">
      <input type="text" id="create-password" v-model="password" placeholder="User Password">
      <button v-on:click="createUser">New User!</button>

    </div>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    </div>
</template>

<script>
import UserService from '../UserService';

export default {
  name: 'UserComponent',
  data() {
    return {
      users: [],
      error: '',
      name: '',
      email: '',
      password: ''
    }
  },
  async created() {
    try {
      this.users = await UserService.getUsers();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createUser() {
      await UserService.insertUser(this.name, this.email, this.password);
      this.users = await UserService.getUsers();
    },
    async deleteUser(id) {
      await UserService.deleteUser(id);
      this.users = await UserService.getUsers();
    }
  }

}
</script>

