<template>
  <div>
    <b-container>
      
      <b-col cols="4">
        <h1>Planets</h1>
        <!--CREATE PLANET HERE-->
        <div class="create-planet">
          <label for="create-planet">Make a Planet</label>
          <input type="text" id="create-planet" v-model="name" placeholder="Planet Name">
          <input type="text" id="planet-question" v-model="question" placeholder="What question does it ask?">
          <input type="text" id="planet-answer" v-model="answer" placeholder="What is its answer?">
          <button v-on:click="createPlanet">New Planet!</button>

        </div>
      </b-col>
      <b-col cols="4">{{ mainPlanet.name }}</b-col>
    </b-container>
    <b-container>
    <hr>
      <p class="error" v-if="error">{{ error }}</p>
      <div class="planets-container">
        <div class="planet"
          v-for="(planet, index) in planets"
          v-bind:item = "planet"
          v-bind:index = "index"
          v-bind:key = "planet._id" 
          v-on:click = "showId(planet._id)"
          v-on:dblclick = "deletePlanet(planet._id)"
        >
          <!--<img alt="emotion image" :src="{{ planet.imgUrl }}">-->
          <p class="name">{{ planet.name }} -- {{ planet.question }}</p>
          <p class="info">{{ planet.imgUrl }} emotion is {{ planet.emotion }}</p>
          <img alt="emotion image" v-bind:src = " `/images/${planet.imgUrl}` ">
          <hr>
        </div>
      </div>
    </b-container>
  </div>
</template>

<script>
import PlanetService from '../PlanetService';

export default {
  name: 'PlanetComponent',
  data() {
    return {
      planets: [],
      error: '',
      name: '',
      question: '',
      answer: '',
      mainPlanet: {
        name: '',
        question: ''
      }
    }
  },
  async created() {
    try {
      this.planets = await PlanetService.getPlanets();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPlanet() {
      await PlanetService.insertPlanet(this.name, this.question);
      this.planets = await PlanetService.getPlanets();
    },
    async deletePlanet(id) {
      await PlanetService.deletePlanet(id);
      this.planets = await PlanetService.getPlanets();
    },
    async showId(id) {
      this.mainPlanet.name = 'herehere' + id;
    }
  }
}
</script>

